.site-main .wp-block-search__inside-wrapper,
.site-footer .wp-block-search__inside-wrapper {
  display: flex;
}
/* -- separator -- */
.wp-block-separator {
  border-color: var(--wp--preset--color--gray-400);
  width: 12.5rem;
}
.wp-block-separator.is-style-wide, .wp-block-separator.is-style-dots {
  width: 100%;
}
.wp-block-separator.is-style-dots::before {
  color: var(--wp--preset--color--gray-400);
}

/* -- social links -- */
.wp-block-social-links {
  gap: 0.75rem;
}
.wp-block-social-links.is-style-logos-only .wp-social-link .wp-block-social-link-anchor {
  color: var(--wp--preset--color--primary-500);
  fill: var(--wp--preset--color--primary-500);
  padding: 0;
}
.wp-block-social-links.has-visible-labels .wp-social-link {
  background-color: var(--wp--preset--color--primary-500);
}
.wp-block-social-links.has-visible-labels .wp-social-link .wp-block-social-link-anchor {
  color: var(--wp--preset--color--white);
  fill: var(--wp--preset--color--white);
  padding: 0.75rem;
}
.wp-block-social-links.is-style-icon .wp-social-link {
  background-color: var(--wp--preset--color--primary-500);
}
.wp-block-social-links.is-style-icon .wp-social-link .wp-block-social-link-anchor {
  color: var(--wp--preset--color--white);
  fill: var(--wp--preset--color--white);
  padding: 0.5rem;
}

