/* Breakpoints */
/* -- table -- */
.wp-block-table,
.wpDataTable,
.dataTable {
  border-collapse: separate !important; /* stylelint-disable-line declaration-no-important */
}
.wp-block-table td,
.wp-block-table th,
.wpDataTable td,
.wpDataTable th,
.dataTable td,
.dataTable th {
  border-color: var(--wp--preset--color--gray-200);
  padding: var(--wp--preset--spacing--10);
}
.wp-block-table thead,
.wpDataTable thead,
.dataTable thead {
  border-bottom: none;
}
.wp-block-table tbody tr:nth-child(odd),
.wpDataTable tbody tr:nth-child(odd),
.dataTable tbody tr:nth-child(odd) {
  background-color: var(--wp--preset--color--primary-50);
}
.wp-block-table tfoot,
.wpDataTable tfoot,
.dataTable tfoot {
  font-weight: 700;
}
@media only screen and (max-width: 600px) {
  .wp-block-table thead,
  .wpDataTable thead,
  .dataTable thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -0.0625rem;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  .wp-block-table tbody tr:nth-child(odd),
  .wpDataTable tbody tr:nth-child(odd),
  .dataTable tbody tr:nth-child(odd) {
    background-color: var(--wp--preset--color--white);
  }
}